
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.story {
  width: 500px;
  height: auto;
  border-radius: 5px;
  border: 1px solid lightgrey;
  margin-bottom: 10px;
  padding: 4px;
}
.key {
  color: grey;
  margin-right: 5px;
}
.title {
  font-weight: bold;
}
.story div {
  padding-bottom: 4px;
}
.description {
  padding-bottom: 10px;
  word-wrap: break-word;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}
.estimate {
  background: lightblue;
  border-radius: 11px;
  padding: 2px 4px;
}
.xml-input {
  display: block;
  width: 700px;
  min-height: 300px;
  border-radius: 5px;
}
.sep {
  height: 15px;
}
.subtitle {
  margin-top: -15px;
  font-size: 13px;
  color: gray;
}
@media print  
{
.noprint {
    display: none;
}
.story{
    page-break-inside: avoid;
    max-height: 99vh;
}
.description {
    font-size: 1.5vh;
}
}
